<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="bsModal"
  >
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span v-if="isNew"></span>
            <span v-else></span>
          </h5>
          <button type="button" class="btn-close bg-white" @click="hideModal"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="image"
                  placeholder=""
                  v-model="tempProduct.imageUrl"
                />
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"
                  > 
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="updatedFile"
                />
              </div>
              <img class="img-fluid mb-5" alt="" :src="tempProduct.imageUrl" />
              <!--  -->
              <div class="mt-5" v-if="tempProduct.imagesUrl">
                <p></p>
                <div
                  class="mb-3 input-group"
                  v-for="(item, key) in tempProduct.imagesUrl"
                  :key="item.key"
                >
                  <img class="img-fluid mb-3" :src="item" />
                  <input
                    type="url"
                    class="form-control form-control"
                    placeholder=""
                    v-model="tempProduct.imagesUrl[key]"
                  />
                  <button
                    type="button"
                    class="btn btn-outline-danger"
                    @click.prevent="tempProduct.imagesUrl.splice(key, 1)"
                  >
                    
                  </button>
                </div>
                <div
                  v-if="
                    tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1] ||
                    !tempProduct.imagesUrl.length
                  "
                >
                  <button
                    class="btn btn-outline-primary btn-sm d-block w-100"
                    @click="tempProduct.imagesUrl.push('')"
                  >
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input
                  type="text"
                  class="form-control"
                  id="title"
                  placeholder=""
                  v-model="tempProduct.title"
                />
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="category"
                    placeholder=""
                    v-model="tempProduct.category"
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="unit"
                    placeholder=""
                    v-model="tempProduct.unit"
                  />
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="origin_price"
                    placeholder=""
                    v-model="tempProduct.origin_price"
                  />
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    type="number"
                    class="form-control"
                    id="price"
                    placeholder=""
                    v-model="tempProduct.price"
                  />
                </div>
              </div>
              <hr />

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="description"
                  placeholder=""
                  v-model="tempProduct.description"
                ></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea
                  type="text"
                  class="form-control"
                  id="content"
                  placeholder=""
                  v-model="tempProduct.content"
                ></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    id="is_enabled"
                    v-model="tempProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled">  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click.prevent="$emit('update-product', tempProduct)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ModalMixin from '@/mixins/ModalMixin';

export default {
  props: {
    productProps: {
      type: Object,
      // 
      default() {
        return {};
      },
    },
    isNew: {
      type: Boolean,
      default: false,
    },
  },
  mixins: [ModalMixin],
  data() {
    return {
      tempProduct: {
        imagesUrl: [],
      },
    };
  },
  watch: {
    productProps() {
      this.tempProduct = this.productProps;
      if (!this.tempProduct.imagesUrl) {
        this.tempProduct.imagesUrl = [];
      }
    },
  },
  methods: {
    updatedFile() {
      const updatedFile = this.$refs.fileInput.files[0];
      // form-data
      const formData = new FormData();
      // file-to-upload
      // APIinputname"file-to-upload"updatedFile
      formData.append('file-to-upload', updatedFile);
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
      this.$http.post(api, formData).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.tempProduct.imageUrl = res.data.imageUrl;
          this.$refs.fileInput.value = '';
        }
      });
    },
  },
};
</script>
